.header {
    max-width: 1440px;
    height: 960px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding-top: 96px;

    .container{
        display: flex;
        flex-direction: row;
        width: 980px;
        height: px-to-rem(330);
        align-items: center;
        justify-content: center;

        .image{
            width:  505px;
            height: 330px;
        }

        .content {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 380px;
            height: 250px;
        }
            

    }
}